import React, { useEffect, useState } from "react";
import Words from "../components/Words";

function Home() {
	const [words, setWords] = useState(JSON.parse(localStorage.getItem("words")));

	useEffect(() => {
		const words = JSON.parse(localStorage.getItem("words"))?.length;

		if (!words) {
			localStorage.setItem(
				"words",
				JSON.stringify([
					{ schlect: "плохо" },
					{ fuchs: "рыжий" },
					{ denken: "думать" },
					{ angenehm: "приятно" },
					{ "das Essen": "еда" },
				])
			);

			setWords([
				{ schlect: "плохо" },
				{ fuchs: "рыжий" },
				{ denken: "думать" },
				{ angenehm: "приятно" },
				{ "das Essen": "еда" },
			]);
		}
	}, []);

	return (
		<main className="_container main">
			<section className="hero">
				{!words ? (
					"Nie masz jeszcze żadnego słowa"
				) : (
					<Words words={words} setWords={setWords} />
				)}
			</section>
		</main>
	);
}

export default Home;
